'use client';

import React, { useState, useEffect } from 'react';
import { useParams } from 'next/navigation';
import LandingNavBar from '@/components/landing/LandingNavBar';
import { programAPI, ApiResponse } from '@/lib/api';
import { Clock, Award, Users, Building, Calendar, BookOpen, GraduationCap, MapPin, ExternalLink, Star, DollarSign } from 'lucide-react';

interface Program {
    _id: string;
    name: string;
    code: string;
    description?: string;
    level: string;
    duration: {
        semesters: number;
        years: number;
    };
    school: {
        _id: string;
        name: string;
        code: string;
    };
    totalCredits: number;
    stats: {
        enrollmentCount: number;
        graduationRate?: number;
        employmentRate?: number;
    };
    requirements?: {
        academic?: string[];
        english?: string;
        other?: string[];
    };
    careerOutcomes?: string[];
    tuition?: {
        domestic?: number;
        international?: number;
        currency?: string;
    };
    isActive: boolean;
    createdAt?: string;
    updatedAt?: string;
}

const ProgramDetailPage = () => {
    const params = useParams();
    const [program, setProgram] = useState<Program | null>(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);

    console.log('üöÄ ProgramDetailPage component initialized');
    console.log('üìã Params received:', params);

    useEffect(() => {
        console.log('üîÑ useEffect triggered');
        console.log('üÜî Program ID from params:', params.id);

        const fetchProgram = async () => {
            try {
                console.log('üì° Starting API call...');
                setLoading(true);
                setError(null);
                
                const programId = params.id as string;
                console.log('üîç Fetching program with ID:', programId);

                // Try different API call approaches
                let response: ApiResponse<Program>;
                
                try {
                    // First try with the ID as is
                    console.log('üîÑ API Call attempt 1: Using ID as-is');
                    response = await programAPI.getProgramById(programId);
                    console.log('‚úÖ API Response (attempt 1):', response);
                } catch (firstError) {
                    console.log('‚ùå API Call attempt 1 failed:', firstError);
                    
                    try {
                        // Second try with uppercase
                        console.log('üîÑ API Call attempt 2: Using uppercase ID');
                        response = await programAPI.getProgramById(programId.toUpperCase());
                        console.log('‚úÖ API Response (attempt 2):', response);
                    } catch (secondError) {
                        console.log('‚ùå API Call attempt 2 failed:', secondError);
                        throw secondError;
                    }
                }
                
                console.log('üìä Full API response structure:', JSON.stringify(response, null, 2));
                
                if (response && response.success && response.data) {
                    console.log('‚úÖ Program data found:', response.data);
                    setProgram(response.data);
                    setError(null);
                } else if (response && !response.success) {
                    console.log('‚ö†Ô∏è API returned unsuccessful response:', response.message);
                    setError(response.message || 'Program not found');
                    setProgram(null);
                } else {
                    console.log('‚ö†Ô∏è No program data in response');
                    setError('Program not found');
                    setProgram(null);
                }
            } catch (err: any) {
                console.error('üí• Error in fetchProgram:', err);
                console.error('üí• Error details:', {
                    message: err.message,
                    status: err.response?.status,
                    statusText: err.response?.statusText,
                    data: err.response?.data,
                    config: err.config
                });
                
                if (err.response?.status === 404) {
                    setError('Program not found');
                } else if (err.response?.status === 500) {
                    setError('Server error. Please try again later.');
                } else if (err.code === 'NETWORK_ERROR') {
                    setError('Network error. Please check your connection.');
                } else {
                    setError(err.message || 'Failed to load program details');
                }
                setProgram(null);
            } finally {
                console.log('üèÅ API call completed, setting loading to false');
                setLoading(false);
            }
        };

        if (params.id) {
            console.log('‚úÖ Program ID exists, calling fetchProgram');
            fetchProgram();
        } else {
            console.log('‚ùå No program ID found in params');
            setError('No program ID provided');
            setLoading(false);
        }
    }, [params.id]);

    console.log('üéØ Current component state:', {
        loading,
        error,
        program: program ? { id: program._id, name: program.name, code: program.code } : null
    });

    if (loading) {
        console.log('‚è≥ Rendering loading state');
        return (
            <div className="min-h-screen bg-[#1a1a1a] text-white">
                <div className="sticky top-0 z-30 bg-[#18191A]">
                    <LandingNavBar />
                </div>
                <div className="h-20" />
                <div className="max-w-6xl mx-auto px-6 py-16">
                    <div className="animate-pulse">
                        <div className="h-12 bg-gray-700 rounded w-2/3 mb-6"></div>
                        <div className="h-6 bg-gray-700 rounded w-1/2 mb-8"></div>
                        <div className="space-y-4">
                            {[1, 2, 3, 4, 5].map(i => (
                                <div key={i} className="h-4 bg-gray-700 rounded"></div>
                            ))}
                        </div>
                    </div>
                </div>
            </div>
        );
    }

    if (error || !program) {
        console.log('‚ùå Rendering error state:', { error, hasProgram: !!program });
        return (
            <div className="min-h-screen bg-[#1a1a1a] text-white">
                <div className="sticky top-0 z-30 bg-[#18191A]">
                    <LandingNavBar />
                </div>
                <div className="h-20" />
                <div className="max-w-6xl mx-auto px-6 py-16 text-center flex flex-col items-center justify-center">
                    <h1 className="text-4xl font-bold mb-4">Program Not Found</h1>
                    <p className="text-gray-400 mb-4">
                        {error || "The program you're looking for doesn't exist."}
                    </p>
                    <p className="text-gray-500 text-sm">
                        Program ID: {params.id}
                    </p>
                    <button 
                        onClick={() => window.location.href = '/programs'}
                        className="mt-6 px-6 py-3 bg-orange-600 hover:bg-orange-700 text-white rounded-lg transition"
                    >
                        Back to Programs
                    </button>
                </div>
            </div>
        );
    }

    console.log('‚úÖ Rendering program details for:', program.name);

    // Sample courses data (since courses might not be populated)
    const sampleCourses = [
        { name: 'Introduction to Programming', code: 'IPC144' },
        { name: 'Data Structures and Algorithms', code: 'DSA456' },
        { name: 'Web Development Fundamentals', code: 'WEB222' },
        { name: 'Software Engineering Principles', code: 'SEP301' }
    ];

    // Sample faculty data
    const sampleFaculty = [
        { name: 'Dr. Arthur Hayes', title: 'Professor of Computer Science' },
        { name: 'Dr. Isabella Rossi', title: 'Associate Professor of Software Engineering' }
    ];

    // Safe data access with fallbacks
    const safeProgram = {
        name: program.name || 'Unknown Program',
        code: program.code || 'N/A',
        description: program.description || 'No description available.',
        level: program.level || 'Unknown',
        duration: program.duration || { years: 0, semesters: 0 },
        school: program.school || { name: 'Unknown School', code: 'N/A' },
        totalCredits: program.totalCredits || 0,
        stats: {
            enrollmentCount: program.stats?.enrollmentCount || 0,
            graduationRate: program.stats?.graduationRate || 0,
            employmentRate: program.stats?.employmentRate || 0
        },
        requirements: program.requirements || {},
        careerOutcomes: program.careerOutcomes || [],
        tuition: program.tuition || {}
    };

    console.log('üìä Safe program data prepared:', safeProgram);

    return (
        <div className="min-h-screen bg-[#1a1a1a] text-white">
            <div className="sticky top-0 z-30 bg-[#18191A]">
                <LandingNavBar />
            </div>
            <div className="h-20" />
            
            <div className="max-w-6xl mx-auto px-6 py-16">
                {/* Header */}
                <div className="mb-12">
                    <div className="flex items-center gap-3 mb-4">
                        <h1 className="text-5xl font-bold">{safeProgram.name}</h1>
                        <span className="px-3 py-1 bg-orange-600 text-white text-sm rounded-full">
                            {safeProgram.code}
                        </span>
                    </div>
                    <p className="text-gray-300 text-lg">
                        {safeProgram.level} program at {safeProgram.school.name}
                    </p>
                </div>

                {/* Program Overview */}
                <section className="mb-12">
                    <h2 className="text-3xl font-bold mb-6">Program Overview</h2>
                    <div className="bg-[#232425] rounded-lg p-8">
                        <p className="text-gray-300 leading-relaxed text-lg">
                            {safeProgram.description}
                        </p>
                    </div>
                </section>

                {/* Program Statistics Grid */}
                <section className="mb-12">
                    <div className="grid grid-cols-2 md:grid-cols-4 gap-6">
                        <div className="bg-[#232425] rounded-lg p-6 text-center">
                            <Clock className="w-8 h-8 text-orange-400 mx-auto mb-3" />
                            <div className="text-2xl font-bold text-white mb-1">{safeProgram.duration.years}</div>
                            <div className="text-gray-400 text-sm">Years</div>
                            <div className="text-gray-500 text-xs">({safeProgram.duration.semesters} Semesters)</div>
                        </div>
                        <div className="bg-[#232425] rounded-lg p-6 text-center">
                            <Award className="w-8 h-8 text-orange-400 mx-auto mb-3" />
                            <div className="text-2xl font-bold text-white mb-1">{safeProgram.totalCredits}</div>
                            <div className="text-gray-400 text-sm">Credits</div>
                        </div>
                        <div className="bg-[#232425] rounded-lg p-6 text-center">
                            <Users className="w-8 h-8 text-orange-400 mx-auto mb-3" />
                            <div className="text-2xl font-bold text-white mb-1">{safeProgram.stats.enrollmentCount}</div>
                            <div className="text-gray-400 text-sm">Students Enrolled</div>
                        </div>
                        <div className="bg-[#232425] rounded-lg p-6 text-center">
                            <Building className="w-8 h-8 text-orange-400 mx-auto mb-3" />
                            <div className="text-lg font-bold text-white mb-1">{safeProgram.level}</div>
                            <div className="text-gray-400 text-sm">Credential</div>
                        </div>
                    </div>
                </section>

                {/* Program Details */}
                <section className="mb-12">
                    <h2 className="text-3xl font-bold mb-6">Program Details</h2>
                    <div className="bg-[#232425] rounded-lg p-8">
                        <div className="grid md:grid-cols-3 gap-8">
                            <div>
                                <h3 className="text-xl font-semibold text-orange-400 mb-4">Objectives</h3>
                                <p className="text-gray-300">
                                    To provide comprehensive knowledge and practical skills in {safeProgram.name.toLowerCase()}, preparing students for successful careers in the field.
                                </p>
                            </div>
                            <div>
                                <h3 className="text-xl font-semibold text-orange-400 mb-4">Curriculum</h3>
                                <p className="text-gray-300">
                                    The program covers essential theoretical concepts and hands-on practical experience through projects and real-world applications.
                                </p>
                            </div>
                            <div>
                                <h3 className="text-xl font-semibold text-orange-400 mb-4">Career Prospects</h3>
                                <p className="text-gray-300">
                                    {safeProgram.careerOutcomes.length > 0 
                                        ? safeProgram.careerOutcomes.join(', ')
                                        : 'Graduates can pursue various career opportunities in their field of study.'
                                    }
                                </p>
                            </div>
                        </div>
                    </div>
                </section>

                {/* Sample Courses */}
                <section className="mb-12">
                    <h2 className="text-3xl font-bold mb-6">Sample Courses</h2>
                    <div className="grid md:grid-cols-2 lg:grid-cols-4 gap-6">
                        {sampleCourses.map((course, index) => (
                            <div key={index} className="bg-[#232425] rounded-lg p-6">
                                <BookOpen className="w-8 h-8 text-orange-400 mb-4" />
                                <h3 className="text-lg font-semibold mb-2">{course.name}</h3>
                                <p className="text-gray-400 text-sm">{course.code}</p>
                            </div>
                        ))}
                    </div>
                </section>

                {/* Faculty */}
                <section className="mb-12">
                    <h2 className="text-3xl font-bold mb-6">Faculty</h2>
                    <div className="grid md:grid-cols-2 gap-6">
                        {sampleFaculty.map((faculty, index) => (
                            <div key={index} className="bg-[#232425] rounded-lg p-6 flex items-center gap-4">
                                <div className="w-16 h-16 bg-gray-600 rounded-full flex items-center justify-center">
                                    <Users className="w-8 h-8 text-gray-400" />
                                </div>
                                <div>
                                    <h3 className="text-lg font-semibold">{faculty.name}</h3>
                                    <p className="text-gray-400">{faculty.title}</p>
                                </div>
                            </div>
                        ))}
                    </div>
                </section>

                {/* Admission Requirements */}
                <section className="mb-12">
                    <h2 className="text-3xl font-bold mb-6">Admission Requirements</h2>
                    <div className="bg-[#232425] rounded-lg p-8">
                        {safeProgram.requirements.academic && safeProgram.requirements.academic.length > 0 ? (
                            <div className="mb-6">
                                <h4 className="text-lg font-semibold mb-3 text-orange-400">Academic Requirements:</h4>
                                <ul className="list-disc list-inside text-gray-300 space-y-2">
                                    {safeProgram.requirements.academic.map((req, index) => (
                                        <li key={index}>{req}</li>
                                    ))}
                                </ul>
                            </div>
                        ) : null}
                        
                        {safeProgram.requirements.english ? (
                            <div className="mb-6">
                                <h4 className="text-lg font-semibold mb-2 text-orange-400">English Requirements:</h4>
                                <p className="text-gray-300">{safeProgram.requirements.english}</p>
                            </div>
                        ) : null}

                        <p className="text-gray-300 mb-4">
                            For detailed admission requirements, please visit the official program page.
                        </p>
                        <a 
                            href={`https://www.senecapolytechnic.ca/programs/fulltime/${safeProgram.code.toLowerCase()}.html`}
                            target="_blank"
                            rel="noopener noreferrer"
                            className="inline-flex items-center gap-2 text-orange-400 hover:text-orange-300 transition"
                        >
                            View Official Admission Requirements
                            <ExternalLink className="w-4 h-4" />
                        </a>
                    </div>
                </section>

                {/* Program Statistics */}
                {(safeProgram.stats.graduationRate > 0 || safeProgram.stats.employmentRate > 0) && (
                    <section className="mb-12">
                        <h2 className="text-3xl font-bold mb-6">Program Statistics</h2>
                        <div className="bg-[#232425] rounded-lg p-8">
                            <div className="grid md:grid-cols-2 gap-8">
                                {safeProgram.stats.graduationRate > 0 && (
                                    <div className="flex justify-between items-center py-4 border-b border-gray-600">
                                        <span className="text-gray-400">Graduation Rate</span>
                                        <span className="text-2xl font-bold text-white">{safeProgram.stats.graduationRate}%</span>
                                    </div>
                                )}
                                {safeProgram.stats.employmentRate > 0 && (
                                    <div className="flex justify-between items-center py-4 border-b border-gray-600">
                                        <span className="text-gray-400">Employment Rate</span>
                                        <span className="text-2xl font-bold text-white">{safeProgram.stats.employmentRate}%</span>
                                    </div>
                                )}
                            </div>
                        </div>
                    </section>
                )}

                {/* Tuition Information */}
                {(safeProgram.tuition.domestic || safeProgram.tuition.international) && (
                    <section className="mb-12">
                        <h2 className="text-3xl font-bold mb-6">Tuition Information</h2>
                        <div className="bg-[#232425] rounded-lg p-8">
                            <div className="grid md:grid-cols-2 gap-8">
                                {safeProgram.tuition.domestic && (
                                    <div className="flex items-center justify-between p-4 bg-[#2a2b2c] rounded-lg">
                                        <div className="flex items-center gap-3">
                                            <DollarSign className="w-6 h-6 text-green-400" />
                                            <span className="text-gray-400">Domestic Students</span>
                                        </div>
                                        <span className="text-xl font-bold text-white">
                                            ${safeProgram.tuition.domestic.toLocaleString()} {safeProgram.tuition.currency || 'CAD'}
                                        </span>
                                    </div>
                                )}
                                {safeProgram.tuition.international && (
                                    <div className="flex items-center justify-between p-4 bg-[#2a2b2c] rounded-lg">
                                        <div className="flex items-center gap-3">
                                            <DollarSign className="w-6 h-6 text-blue-400" />
                                            <span className="text-gray-400">International Students</span>
                                        </div>
                                        <span className="text-xl font-bold text-white">
                                            ${safeProgram.tuition.international.toLocaleString()} {safeProgram.tuition.currency || 'CAD'}
                                        </span>
                                    </div>
                                )}
                            </div>
                        </div>
                    </section>
                )}

                {/* Call to Action */}
                <section className="mb-12">
                    <div className="bg-gradient-to-r from-orange-600 to-red-600 rounded-lg p-8 text-center">
                        <h2 className="text-3xl font-bold mb-4">Interested in This Program?</h2>
                        <p className="text-lg mb-6 opacity-90">
                            Learn more about admission requirements and how to apply to {safeProgram.name}.
                        </p>
                        <div className="flex flex-col sm:flex-row gap-4 justify-center">
                            <a 
                                href={`https://www.senecapolytechnic.ca/programs/fulltime/${safeProgram.code.toLowerCase()}.html`}
                                target="_blank"
                                rel="noopener noreferrer"
                                className="px-8 py-3 bg-white text-orange-600 font-semibold rounded-lg hover:bg-gray-100 transition"
                            >
                                View Official Program Page
                            </a>
                            <a
                                href="https://www.senecapolytechnic.ca/admissions/how-to-apply.html"
                                target="_blank"
                                rel="noopener noreferrer"
                                className="px-8 py-3 border-2 border-white text-white font-semibold rounded-lg hover:bg-white hover:text-orange-600 transition"
                            >
                                Apply Now
                            </a>
                        </div>
                    </div>
                </section>

                {/* Debug Information (only in development) */}
                {process.env.NODE_ENV === 'development' && (
                    <section className="mb-12 bg-gray-800 rounded-lg p-6">
                        <h3 className="text-lg font-bold mb-4 text-yellow-400">Debug Information</h3>
                        <div className="text-xs text-gray-300 space-y-2">
                            <p><strong>Program ID:</strong> {program._id}</p>
                            <p><strong>Program Code:</strong> {program.code}</p>
                            <p><strong>School ID:</strong> {program.school._id}</p>
                            <p><strong>Active:</strong> {program.isActive ? 'Yes' : 'No'}</p>
                            <p><strong>Created:</strong> {program.createdAt}</p>
                            <p><strong>Updated:</strong> {program.updatedAt}</p>
                        </div>
                    </section>
                )}
            </div>
        </div>
    );
};

export default ProgramDetailPage;


